import Display from '../display/display';

import './style.css';

class Carousel {
  constructor(container, info, widthNum) {
    this.container = container;
    this.info = info;
    this.carousel = document.createElement('div');
    this.buttons = document.createElement('div');
    this.buttonLeft = document.createElement('button');
    this.buttonRight = document.createElement('button');
    this.page = document.createElement('div');
    this.pageTarget = 1;
    this.x = 0;
    this.pageMuch = 2;
    this.muchThen = -4800;
    this.widthNum = widthNum;
  }

  viewCarousel() {
    this.carousel.classList.add('container__carousel');
    this.container.appendChild(this.carousel);
    this.viewButtonsContainer();
  }

  viewButtonsContainer() {
    this.buttons.classList.add('container__buttons');
    this.container.appendChild(this.buttons);
    this.viewButtons();
  }

  viewButtons() {
    this.buttonLeft.classList.add('buttons');
    this.buttons.appendChild(this.buttonLeft);
    this.page.classList.add('page');
    this.buttons.appendChild(this.page);
    this.pageNumber();
    this.buttonRight.classList.add('buttons');
    this.buttons.appendChild(this.buttonRight);
    this.buttonLeft.textContent = '<';
    this.buttonRight.textContent = '>';
    this.addListeners();
  }

  pageNumber() {
    this.page.textContent = `${this.pageTarget}`;
  }

  addListeners() {
    this.carouselPosition = 0;
    this.buttonLeft.addEventListener('click', () => {
      this.leftPage();
    });
    this.buttonRight.addEventListener('click', () => {
      this.rightPage();
    });
    this.caruoselDragAndDrop();
    this.viewElems();
  }

  leftPage() {
    if (this.carouselPosition < 0) {
      this.carouselPosition = this.carouselPosition + this.widthNum;
      this.carousel.style.left = `${this.carouselPosition}px`;
      this.pageTarget -= 1;
      this.pageTargetMinus();
      this.pageNumber();
      this.carouselPositionMinus(this.carousel);
    }
  }

  rightPage() {
    if (this.carouselPosition > this.muchThen) {
      this.carouselPosition = this.carouselPosition - this.widthNum;
      this.carousel.style.left = `${this.carouselPosition}px`;
      this.pageTarget += 1;
      this.carouselSoMuch(this.carousel);
      if (this.carouselPosition <= this.muchThen) {
        this.muchThen -= 7200;
      }
      this.pageTargetMinus();
      this.pageNumber();
      this.carouselPositionMinus(this.carousel);
    }
  }

  caruoselDragAndDrop() {
    const obj = this.carousel;
    this.carousel.onmousedown = (event) => {
      function getCoords(elem) {
        const box = elem.getBoundingClientRect();
        return {
          left: box.left + window.pageXOffset,
        };
      }

      const coords = getCoords(obj);
      const shiftX = event.pageX - coords.left;

      function moveAt(e) {
        obj.style.left = `${e.pageX - shiftX}px`;
      }

      moveAt(event);

      obj.style.transition = '0s';
      document.onmousemove = (e) => {
        moveAt(e);
      };

      obj.onmouseup = () => {
        const str = obj.style.left;
        obj.style.transition = '0.5s';
        document.onmousemove = null;
        obj.onmouseup = null;
        this.carouselPosition = parseInt(str, 10);
        this.carouselPositionMinus(obj);
        this.pageTarget = -(Math.floor(this.carouselPosition / this.widthNum));
        this.pageTargetMinus();
        this.pageNumber();
        this.carouselSoMuch(obj);
      };
    };
    obj.ondragstart = function dragStart() {
      return false;
    };
  }

  pageTargetMinus() {
    if (this.pageTarget < 1) {
      this.pageTarget = 1;
    }
  }

  carouselPositionMinus(elem) {
    const element = elem;
    if (this.carouselPosition > 0) {
      element.style.left = '0px';
    }
  }

  carouselSoMuch(elem) {
    const element = elem;
    if (this.carouselPosition <= this.muchThen) {
      element.style.width = `${7200 * this.pageMuch}px`;
      this.pageMuch += 1;
      this.muchThen -= 7200;
    }
  }

  viewElems() {
    for (let i = 0; i < this.info.videoTitles.length; i += 1) {
      this.elem = document.createElement('div');
      this.elem.classList.add('elems');
      this.carousel.appendChild(this.elem);
      this.display = new Display(i, this.elem, this.info);
      this.display.display();
    }
  }
}

export default Carousel;
