import UrlDestructor from '../url/url';

import './style.css';

class Search {
  constructor(root, widthNum) {
    this.root = root;
    this.searchString = '';
    this.fullSearchString = '';
    this.searchLine = document.createElement('div');
    this.searchInput = document.createElement('input');
    this.searchButton = document.createElement('button');
    this.url = 'https://www.googleapis.com/youtube/v3/search?key=AIzaSyA6lRJMA1OUVTYdLhdy6p61FGQqI4cp9v8&type=video&part=snippet&maxResults=15&q=';
    this.widthNum = widthNum;
  }

  showSearch() {
    this.searchLine.classList.add('search-line');
    this.root.appendChild(this.searchLine);
    this.searchInput.classList.add('search-line__search-input');
    this.searchButton.innerHTML = 'Search';
    this.searchButton.classList.add('search-line__search-button');
    this.searchLine.appendChild(this.searchInput);
    this.searchLine.appendChild(this.searchButton);
    this.addListeners();
  }

  addListeners() {
    this.searchInput.addEventListener('change', (e) => {
      this.saveSearchString(e.target.value);
    });
    this.searchButton.addEventListener('click', () => {
      this.search();
    });
  }

  saveSearchString(string) {
    this.searchString = string;
  }

  search() {
    this.fullSearchString = this.url + this.searchString;
    this.urlDestructor = new UrlDestructor(this.root, this.fullSearchString, this.widthNum);
    this.urlDestructor.destructor();
  }
}

export default Search;
