import Container from '../container/container';

class Base {
  constructor(root, dataInfo, dataView, widthNum) {
    this.root = root;
    this.info = {
      videoTitles: [],
      picturesUrl: [],
      descriptions: [],
      chanalTitles: [],
      publishedAtItems: [],
      viewCounts: [],
      id: [],
      nextPageToken: [],
    };
    this.dataInfo = dataInfo;
    this.dataView = dataView;
    this.widthNum = widthNum;
  }

  fullDecomposition() {
    this.decompositionVideoTitle();
    this.decompositionPicture();
    this.decompositionDescription();
    this.decompositionChanalTitle();
    this.decompositionPublishedAt();
    this.decompositionViews();
    this.decompositionId();
    this.decompositionToken();
    this.startView();
  }

  decompositionVideoTitle() {
    this.info.videoTitles = this.dataInfo.items.map(clip => clip.snippet.title);
  }

  decompositionPicture() {
    this.info.picturesUrl = this.dataInfo.items.map(clip => clip.snippet.thumbnails.high.url);
  }

  decompositionDescription() {
    this.info.descriptions = this.dataInfo.items.map(clip => clip.snippet.description);
  }

  decompositionChanalTitle() {
    this.info.chanalTitles = this.dataInfo.items.map(clip => clip.snippet.channelTitle);
  }

  decompositionPublishedAt() {
    this.info.publishedAtItems = this.dataInfo.items.map(clip => clip.snippet.publishedAt);
  }

  decompositionViews() {
    this.info.viewCounts = this.dataView.items.map(clip => clip.statistics.viewCount);
  }

  decompositionId() {
    this.info.id = this.dataInfo.items.map(clip => clip.id.videoId);
  }

  decompositionToken() {
    this.info.nextPageToken = this.dataInfo.nextPageToken;
  }

  startView() {
    this.container = new Container(this.root, this.info, this.widthNum);
    this.container.viewContainer();
  }
}

export default Base;
