import Base from '../base/base';

class UrlDestructor {
  constructor(root, fullSearchString, widthNum) {
    this.root = root;
    this.fullSearchString = fullSearchString;
    this.urlForView = '';
    this.id = [];
    this.widthNum = widthNum;
  }

  async destructor() {
    const urlInfo = this.fullSearchString;

    const responceInfo = await fetch(urlInfo);
    const dataInfo = await responceInfo.json();

    this.id = dataInfo.items.map(clip => clip.id.videoId);
    this.urlForView = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyA6lRJMA1OUVTYdLhdy6p61FGQqI4cp9v8&id=${this.id.join(',')}&part=snippet,statistics`;
    const urlView = this.urlForView;

    const responceView = await fetch(urlView);
    const dataView = await responceView.json();

    this.base = new Base(this.root, dataInfo, dataView, this.widthNum);
    this.base.fullDecomposition();
  }
}

export default UrlDestructor;
