import './style.css';

class Display {
  constructor(i, elem, info) {
    this.elem = elem;
    this.info = info;
    this.i = i;
  }

  display() {
    this.displayVideoTitle();
    this.displayPictureUrl();
    this.displayChanalTitle();
    this.displayPublishedAt();
    this.displayViews();
    this.displayDescription();
  }

  displayVideoTitle() {
    this.videoTitle = document.createElement('a');
    this.videoTitle.href = `https://www.youtube.com/watch?v=${this.info.id[this.i]}`;
    this.videoTitle.classList.add('video-title');
    this.elem.appendChild(this.videoTitle);
    this.content = this.info.videoTitles[this.i];
    this.videoTitle.textContent = `${this.content}`;
  }

  displayPictureUrl() {
    this.picture = document.createElement('img');
    this.picture.src = `${this.info.picturesUrl[this.i]}`;
    this.elem.appendChild(this.picture);
    this.picture.classList.add('video-image');
  }

  displayChanalTitle() {
    this.chanalTitle = document.createElement('h3');
    this.chanalTitle.classList.add('chanal-title');
    this.elem.appendChild(this.chanalTitle);
    this.content = this.info.chanalTitles[this.i];
    this.chanalTitle.textContent = `Chanal: ${this.content}`;
  }

  displayPublishedAt() {
    this.publishedAt = document.createElement('h3');
    this.publishedAt.classList.add('published-at');
    this.elem.appendChild(this.publishedAt);
    this.content = this.info.publishedAtItems[this.i];
    this.publishedAt.textContent = `Published At: ${this.content}`;
  }

  displayViews() {
    this.views = document.createElement('h3');
    this.views.classList.add('views');
    this.elem.appendChild(this.views);
    this.content = this.info.viewCounts[this.i];
    this.views.textContent = `Views: ${this.content}`;
  }

  displayDescription() {
    this.description = document.createElement('p');
    this.description.classList.add('description');
    this.elem.appendChild(this.description);
    this.content = this.info.descriptions[this.i];
    this.description.textContent = `Description: ${this.content}`;
  }
}

export default Display;
