import Search from '../search/search';

import './style.css';

class General {
  constructor(root) {
    this.root = root;
    this.root.classList.add('general');
    this.clientWidth = document.documentElement.clientWidth;
    this.widthNum = 0;
  }

  start() {
    this.width();
    this.client = new Search(this.root, this.widthNum);
    this.client.showSearch();
  }

  width() {
    if (this.clientWidth >= 1920) {
      this.root.style.width = `${1920}px`;
      this.widthNum = 1920;
    } else if (this.clientWidth < 1920 && this.clientWidth >= 1440) {
      this.root.style.width = `${1440}px`;
      this.widthNum = 1440;
    } else if (this.clientWidth < 1440 && this.clientWidth >= 960) {
      this.root.style.width = `${960}px`;
      this.widthNum = 960;
    } else if (this.clientWidth < 960 && this.clientWidthh >= 480) {
      this.root.style.width = `${480}px`;
      this.widthNum = 480;
    }
  }
}

export default General;
